function mapStateToProps(state) {
  const { authentication } = state;
  const { user } = authentication;
  return {
    user
  };
}

const connectedAdminLayout = connect(mapStateToProps)(AdminLayout);
export { connectedAdminLayout as AdminLayout };

const getWidth = () => {
  const isSSR = typeof window === 'undefined'

  return isSSR ? Responsive.onlyTablet.minWidth : window.innerWidth
}

const ResponsiveContainer = ({ children, user, hideMenu }) => (
  <>
    <Responsive minWidth={Responsive.onlyTablet.minWidth}>
      <DesktopContainer user={user} hideMenu={hideMenu}>{children}</DesktopContainer>
    </Responsive>
    <Responsive {...Responsive.onlyMobile}>
      <MobileContainer user={user} hideMenu={hideMenu}>{children}</MobileContainer>
    </Responsive>
  </>
)

class MobileContainer extends Component {
  state = {}

  componentDidMount() {
    document.querySelector('html').setAttribute('dir', 'rtl')
  }

  handleSidebarHide = () => this.setState({ sidebarOpened: false })

  handleToggle = () => this.setState({ sidebarOpened: true })

  render() {
    const { children, user, hideMenu } = this.props
    const { sidebarOpened } = this.state

    return (
      <Responsive
        as={Sidebar.Pushable}
        getWidth={getWidth}
        maxWidth={Responsive.onlyMobile.maxWidth}
      >
        <Sidebar
          animation='push'
          onHide={this.handleSidebarHide}
          visible={sidebarOpened}
        >
          <div className="mobile-menu">
            <NavMenu handleSidebarHide={this.handleSidebarHide} />
          </div>
        </Sidebar>

        <Sidebar.Pusher dimmed={sidebarOpened}>

          <div className="header" >

            <Container>
              <Menu text style={{ margin: 0 }}>
                <Menu.Menu position='right'>
                  <Menu.Item onClick={this.handleToggle}>
                    <FaBars className="mdi" color='#fff'/>
                    {/* <Icon inverted name='sidebar' size='large' /> */}
                  </Menu.Item>

                  <Menu.Item fitted header style={{ minWidth: 80, padding: '0 20px' }}>
                  <img className="avatar-sm" src="/admin/images/default-avatar.png" alt="" />
                    <div className="user-fullname">
                      کاوشگر
                  <br />

                      {/* {user.name && user.name}
                  {user.surname && ' ' + user.surname} */}
                      <span>پنل مدیریتی</span>
                    </div>
                  </Menu.Item>
                </Menu.Menu>
                <UserDropDown />
              </Menu>
            </Container>

          </div>

          <div className="row">
            <div className="col-md-10 col-md-offset-1">
              <div className="main-wrapper">
                {children}
              </div>
            </div>
          </div>

        </Sidebar.Pusher>
      </Responsive>
    )
  }
}

class DesktopContainer extends Component {
  state = { userFullName: '' }

  componentDidMount() {
    document.querySelector('html').setAttribute('dir', 'rtl')
    // document.querySelector('html').setAttribute('lang', 'ar')



  }

  hideFixedMenu = () => this.setState({ fixed: false })
  showFixedMenu = () => this.setState({ fixed: true })

  render() {
    const { children, user, hideMenu } = this.props
    const { fixed, userFullName } = this.state

    return (
      <Responsive getWidth={getWidth} minWidth={Responsive.onlyTablet.minWidth}>
        <Visibility
          once={false}
          onBottomPassed={this.showFixedMenu}
          onBottomPassedReverse={this.hideFixedMenu}
        >
          <div className={hideMenu ? "header red" : "header"}>

            <div className="row">
              <div className="col-md-10 col-md-offset-1">
                <div className={hideMenu ? "text-center" : "pull-left"} style={{ padding: hideMenu ? "10px": "18px 10px 0 0" }}>
                  <Link to="/login" ><IoIosPower color="#fff" size="30px" /></Link>
                </div>
                
                {!hideMenu &&
                <>
                <img className="avatar" src="/admin/images/default-avatar.png" alt="" />
                <div className="user-fullname">
                کاوشگر
    
                  <br />

                  {/* {user.name && user.name}
                  {user.surname && ' ' + user.surname} */}
                  <span>{user && user.role && user.roleId == 2 ? 'پنل شرکتی' : 'پنل مدیریتی'}</span>
                </div>
                </>
  }
              </div>
            </div>

          </div>
          {!hideMenu &&
          <div className="top-navbar" >
            <div className="row">
              <div className="col-md-10 col-md-offset-1">
                <div className="main-menu">
                  <NavMenu />
                </div>
              </div>
            </div>
          </div>
  }
        </Visibility>

        <div className="row">
          <div className="col-md-12">
            <div className={hideMenu ? "main-wrapper hideMenu" : "main-wrapper"}>
              {children}
            </div>
          </div>
        </div>
      </Responsive>
    )
  }
}

function UserDropDown() {

  return (
    <div>
    </div>
  )

}