import React from 'react'
import { Route, Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { FaTasks, FaWarehouse, FaUserCog, FaAward, FaUsers, FaUserCheck, FaToolbox } from 'react-icons/fa'
import { MdDashboard, MdSettings, MdShoppingCart, MdComment, MdCardMembership } from 'react-icons/md'
