import React, { Component } from 'react'
import QRCode from 'qrcode.react'
import AssignGift from './AssignGift'
import { MdCardMembership, MdClose } from 'react-icons/md'
import { shopsService } from '../../_services/shops.service'
import { toastr } from 'react-redux-toastr'

class QrCodes extends Component {

  constructor(props, context) {
    super(props, context);

    var pageSize = parseInt(localStorage.getItem('pageSizeForProducts'), 0)
    this.state = {
      data: this.props.data,
      loading: true,
      model: {},
      showAssignGift: false,
    }

  }

  handleModalAssignGift = (model, i) => {

    model["index"] = i;

    this.setState({ model: model, showAssignGift: true })

  }

  handleRemoveGift = (model, i) => {

    let dto = {
      uuid: model.uuid
    }

    console.log(JSON.stringify(dto));

    shopsService.removeGift(JSON.stringify(dto))

      .then((res) => {

        console.log(res.data.item_code);

        toastr.success('حذف جایزه انجام شد');

        let val = res.data.item_code;

        let tmp = this.state.data;

        tmp.codes.item_codes[i] = val;

        console.log(tmp);

        this.setState({ model: {}, showAssignGift: false, data: tmp })
        // props.onCloseAndReload(tmp)

      })
      .catch((error) => {
        console.log(error)
      });

    // model["index"] = i;

    // this.setState({ model: model, showAssignGift: true })

  }

  handleAssignModalClose = () => {

    this.setState({ model: {}, showAssignGift: false })

  }

  handleCloseAndReload = (val) => {

    let tmp = this.state.data;

    tmp.codes.item_codes[val.index] = val;

    this.setState({ model: {}, showAssignGift: false, data: tmp })

  }

  render() {
    const { loading, data, showAssignGift, model } = this.state;
    return (
      <div>
        <table className="table validator-table" style={{ marginBottom: 5 }}>
          <thead>
            <tr>
              <th className="text-center">ردیف</th>
              <th className="text-center">نام محصول</th>
              <th className="text-center">برند</th>
              <th className="text-center">کد منحصر بفرد کالا</th>
              <th className="text-center">جایزه</th>
              <th className="text-center">QrCode</th>
            </tr>
          </thead>
          <tbody>
            {data.codes && data.codes.item_codes.map((item, index) => (
              <tr key={"p-" + index} style={{ background: item.has_gift ? '#42f3be' : 'unset' }}>
                <td>{index + 1}</td>
                <td>
                  {data.name}
                </td>
                <td>
                  {data.brand}
                </td>
                <td>
                  {item.uuid}
                </td>
                <td>
                  {item.has_gift ?
                    <a onClick={() => this.handleRemoveGift(item, index)} className="btn btn-sm btn-simple btn-danger bmd-ripple bmd-flat-btn flex-align-center flex-center">
                      <span style={{fontSize: '14px', color: '#000'}}>{item.gift.value}</span>
                      <MdClose size="28px" />
                    </a>

                    :
                    <a onClick={() => this.handleModalAssignGift(item, index)} className="btn btn-sm btn-simple btn-info bmd-ripple bmd-flat-btn"><MdCardMembership size="28px" /></a>
                  }
                </td>
                <td>
                  <QRCode value={item.uuid} />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        <AssignGift data={model} open={showAssignGift} onClose={() => this.handleAssignModalClose()} onCloseAndReload={(val) => this.handleCloseAndReload(val)} />
      </div >
    )
  }
}

export default QrCodes;