export const LineChart = (props) => {

  const [data, setData] = useState(props.data);

  useEffect(() => {
    setData(props.data)
  })

  return (
    <div style={{ direction: 'ltr' }} className="timeChart-containr">
      {data &&
        <Line
          data={{
            labels: data.map(el => (el.day)),
            datasets: [{
              label: "ثبت نام ماه قبل",
              borderColor: '#36A2EB',
              backgroundColor: '#36A2EB',
              // borderWidth: 2,
              data: data.map(el => (el.last)),
              fill: false
            },{
              label: "ثبت نام این ماه",
              borderColor: '#FF6384',
              backgroundColor: '#FF6384',
              // borderWidth: 2,
              data: data.map(el => (el.this)),
              fill: false
            }]
          }}
          options={options}
        />
      }
    </div>
  )
}

