import React, { Component } from 'react'
import { giftsService } from '../../_services/gifts.service'
import { Dropdown } from 'semantic-ui-react'


class GiftSelect extends Component {

  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      defaultValue: this.props.defaultValue,
      data: [],
    };
  }

  componentWillMount() {
    this.getData();
  }

  getData() {

    this.setState({ loading: true })
    giftsService.getAll()
      .then((res) => {
        let data = res.data.gifts.map(elm => ({ "text": elm.name, "value": elm.id }))
        this.setState({ data: data, loading: false, isSubmitting: false });
      }).catch((error) => {
        this.setState({ loading: false })
        // console.log(error)
      });

  };


  async componentWillReceiveProps(nextProps) {
    if (nextProps.defaultValue !== this.state.defaultValue) {
      this.setState({ defaultValue: nextProps.defaultValue });
    }
  }

  handleChange = (e) => {

    let selected = e;
    this.setState({ selected: selected })
    this.props.onChange(selected)

  }

  render() {

    const { data, loading, defaultValue, disabled } = this.state;
    const { clearable, className } = this.props;

    return (
      <Dropdown
        placeholder=''
        fluid
        search
        selection
        basic
        onChange={(e, data) => this.handleChange(data.value)}
        value={defaultValue}

        className={className}
        options={data}
        wrapSelection={false}
        selectOnBlur={false}
        searchInput={{ autoFocus: false }}
        loading={loading}
        disabled={disabled}
        clearable={clearable}
      />
    );
  }
}
export default GiftSelect;
