import axios from 'axios'
import { handleSuccess, handleError } from './axiosHandler';
import { authHeader, authHeaderJson } from '../_helpers/auth-header';

import config from '../config';
const base_url = config.apiUrl;

const model = 'shops/';

export const shopsService = {
  getAllPaginate,
  getAllProducts,
  getById,
  add,
  addProduct,
  assignGift,
  removeGift,
  edit,
  delete: _delete,
};

function getAllPaginate(dto) {
  console.log(dto);
  
  const url = base_url
    + 'shops?page=' + dto.page
    + '&per_page=' + dto.pageSize
    + (dto.name ? '&name=' + dto.name : '')
    + (dto.state_id ? '&state_id=' + dto.state_id : '')
    + (dto.city_id ? '&city_id=' + dto.city_id : '')
    + (dto.order ? '&order=' + dto.order : '')
    + (dto.desc != undefined ? '&desc=' + dto.desc : '')
  console.log(url);

  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeaderJson() });
}

function getAllProducts(dto) {
  const url = base_url
    + 'shops/' + dto.shop_id + '/items?page=' + dto.page
    + '&per_page=' + dto.pageSize
    + (dto.name ? '&name=' + dto.name : '')
    + (dto.order ? '&order=' + dto.order : '')
    + (dto.desc != undefined ? '&desc=' + dto.desc : '')
  console.log(url);

  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeaderJson() });
}

function getById(id) {
  const url = base_url + model + id;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader() });
}

function add(dto) {
  const url = base_url + model;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.post(url, dto, { headers: authHeaderJson() });
}

function assignGift(dto) {
  const url = base_url + 'assign_gift';
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.post(url, dto, { headers: authHeaderJson() });
}

function removeGift(dto) {
  const url = base_url + 'remove_gift';
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.post(url, dto, { headers: authHeaderJson() });
}

function addProduct(dto) {
  
  const url = base_url + model + dto.shop_id + '/items';
  console.log(url);
  
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.post(url, dto, { headers: authHeaderJson() });
}

function edit(id, dto) {
  const url = base_url + model + id;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.put(url, dto, { headers: authHeaderJson() });
}

function _delete(id) {
  const url = base_url + model + id;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.delete(url, { headers: authHeader() });
}
