import React, { Component } from 'react'
// import PropTypes from 'prop-types'
import ReactSelect from 'react-select'
import SimpleValue from 'react-select-simple-value'
import { citiesService } from '../../_services/cities.service'

export const Select = ({ options, value, ...props }) => (
  <SimpleValue options={options} value={value}>
    {simpleProps => <ReactSelect {...props} {...simpleProps} />}
  </SimpleValue>
);

class ProvinceSelect extends Component {

  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      defaultValue: '',
      data: [],
      state_id: this.props.state_id,
      value: this.props.defaultValue || {},
    };

  }
  // componentWillMount() {
  //   if (this.state.state_id) {
  //     this.getData(this.state.state_id)
  //   }
  // }
  componentDidMount() {
    this.getData(this.props.state_id)
  }

  componentWillReceiveProps(nextProps) {
    // // console.log(nextProps.state_id);

    if (nextProps.state_id && nextProps.state_id !== this.state.state_id) {
      var state_id = nextProps.state_id;

      this.setState({ state_id: state_id })

      this.getData(state_id);

    } else if (nextProps.state_id == undefined) {

      this.setState({ state_id: undefined, data: [] })

    }

    if (nextProps.defaultValue !== this.state.value) {

      this.setState({ value: nextProps.defaultValue });

    }
  }

  getData = async (state_id) => {
    // // console.log(state_id);
    this.setState({ loading: true });

    await citiesService.getAll(state_id)
      .then((res) => {
        // console.log(res.data)
        let data = res.data.cities.map(elm => ({ "label": elm.name, "value": elm.id }))

        this.setState({ data: data, loading: false });
      })
      .catch((error) => {
        this.handleError(error)
      });
  };

  handleError(error) {
    this.setState({ loading: false });
    // toastr.warning('مشکلی پیش آمد', error);
  }

  handleChange = e => {
    this.props.onChange(e);
  }

  render() {
    const customStyles = {
      // option: (provided, state) => ({
      //   ...provided,
      //
      // }),
      control: (provided) => ({
        // none of react-select's styles are passed to <Control />
        ...provided,
        background: '#f6f6f6',
        borderRadius: 20,
        // height: 40,
        boxShadow: 'none',
        border: 'none',
        paddingRight: '12',
      }),
      indicatorSeparator: () => ({
        display: 'none'
      }),

    }
    const { loading, data, state_id, value } = this.state;
    const { disabled, className, isClearable, placeholder } = this.props;

    return (
      <ReactSelect
        isClearable={isClearable}
        isDisabled={state_id ? false : true}
        placeholder={placeholder ? placeholder : ''}
        styles={customStyles}
        options={data}
        onChange={this.handleChange}
        value={value ? data.filter(el => el.value === value) : null}
        className={className ? className : "select"}
        isLoading={loading}
        noOptionsMessage={() => "نتیجه ای یافت نشد"}
        loadingMessage={() => "در حال دریافت اطلاعات از سرور"}
      />
    );
  }
}
export default ProvinceSelect;
