import React from 'react'
import { Router, Route, Redirect, Switch } from 'react-router-dom'
import { AdminLayout } from '../_components/Shared/Layout/AdminLayout'
import { history } from '../_helpers'
import { connect } from 'react-redux'

// import Home from '../containers/home'

// import { PrivateRoute } from '../_components'
import { LoginPage } from '../LoginPage'

import AdminDashboard from '../_components/Dashboard/AdminDashboard/AdminDashboard';

import Gifts from '../_components/Gifts/index';

import Shops from '../_components/Shops/index';
import Products from '../_components/Products/index';

export const PrivateRoute = ({ component: Component, user, ...rest }) => (
  <Route {...rest} render={props => (
    user && user.token
      ? <AdminLayout><Component {...props} /></AdminLayout>
      : <Redirect to={{ pathname: '/login', state: { from: props.location } }} />
  )} />
)

export const OnlyAdminRoute = ({ component: Component, user, ...rest }) => (
  <Route {...rest} render={props => (
    user && user.user.username !== undefined && user.user.username === "admin"
      ? <AdminLayout><Component {...props} /></AdminLayout>
      : <Redirect to={{ pathname: '/', state: { from: props.location } }} />
  )} />
)

export const AdminRoute = ({ component: Component, user, ...rest }) => (
  <Route {...rest} render={props => (
    user && user.userType !== undefined && [0, 1].includes(user.userType)
      ? <AdminLayout><Component {...props} /></AdminLayout>
      : <Redirect to={{ pathname: '/', state: { from: props.location } }} />
  )} />
)


class App extends React.Component {
  render() {
    const { user } = this.props;
    return (
      <Router history={history}>
        <Switch>

          <PrivateRoute user={user} exact path='/' component={AdminDashboard} />
          <PrivateRoute user={user} exact path='/admin' component={AdminDashboard} />

          <Route path="/login" exact component={LoginPage} />

          <OnlyAdminRoute user={user} exact path="/admin/gifts" component={Gifts} />

          <OnlyAdminRoute user={user} exact path="/admin/Shops" component={Shops} />
          <OnlyAdminRoute user={user} exact path="/admin/Shops/:id" component={Shops} />
          
          <OnlyAdminRoute user={user} exact path="/admin/Shops/:id/Products" component={Products} />

        </Switch>
      </Router>

    );
  }
}

function mapStateToProps(state) {
  const { authentication } = state;
  const { user } = authentication;
  return {
    user
  };
}

const connectedApp = connect(mapStateToProps)(App);
export { connectedApp as App };
