/*eslint-env jquery*/
import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { toastr } from 'react-redux-toastr'
import MySweetAlert from '../Shared/MySweetAlert'
import Loading from '../Shared/Loading'
import PicUploader from '../Shared/PicUploader'
// import { productsService } from '../../_services/products.service'
import { shopsService } from '../../_services/shops.service'
import { AiOutlineQrcode } from 'react-icons/ai'
import { FaPlus, FaToggleOff } from 'react-icons/fa'
import { FiEdit } from 'react-icons/fi'
import { FaFileExcel } from 'react-icons/fa'
import { FiTrash2 } from 'react-icons/fi'
import { GiMagnifyingGlass } from 'react-icons/gi'
import { MdPlaylistAddCheck, MdAdd } from 'react-icons/md'
import config from '../../config'
import { AiOutlineLoading, AiOutlineSave } from "react-icons/ai";
import { DecimalInput } from 'react-hichestan-numberinput'

import { Field, FastField, ErrorMessage, getIn, Formik } from "formik"
import * as Yup from 'yup'
import ErrorPopUp from '../Shared/ErrorPopUp'
import ProvinceSelect from '../Shared/ProvinceSelect'
import CitySelect from '../Shared/CitySelect'

import QrCodes from './QrCodes'

import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';

import Fab from '@material-ui/core/Fab'
import { MdClose } from 'react-icons/md'

// Import React Table
import matchSorter from 'match-sorter'
import ReactTable from "react-table"
import "react-table/react-table.css"
import { Input } from 'antd'
import { CSVLink } from "react-csv";
import moment from 'moment-jalaali'
moment.loadPersian({ dialect: 'persian-modern' })

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});


const requestData = (pageSize, page, sorted, filtered, dto) => {
  console.log(dto)
  return new Promise(async (resolve, reject) => {

    // var dto = {};
    dto["pageSize"] = pageSize;
    dto["page"] = page + 1;
    if (sorted && sorted.length > 0) {
      dto["order"] = sorted[0].id;
      dto["desc"] = sorted[0].desc;
    }
    // console.log(JSON.stringify(dto));

    var result = [];
    await shopsService.getAllProducts(dto)
      .then((res) => {
        // console.log(res.data)
        result = res.data

      })
      .catch((err) => {
        setTimeout(() => requestData(pageSize, page, sorted, filtered, dto), 5000);
        // // console.log(err);

      })


    setTimeout(() => resolve(result), 100);
  });
};

class Products extends Component {

  constructor(props, context) {
    super(props, context);

    var pageSize = parseInt(localStorage.getItem('pageSizeForProducts'), 0)
    this.state = {
      initialItems: [],
      data: [],
      loading: true,
      confirmDeleteShow: false,
      requiredItem: 0,
      deleteItemIndex: 0,
      model: {},
      recordsCount: 0,
      dataToDownload: [],
      downloading: false,
      totalRecords: '',

      pageSize: pageSize ? pageSize : 10,
      dto: {
        page: 0,
        pageSize: pageSize,
        shop_id: this.props.id,
      },
    }

  }

  // componentDidMount() {
  //   if (this.props.match.params.id) {
  //     let dto = this.state.dto;
  //     dto["shop_id"] = this.props.match.params.id;
  //     this.setState({ dto: dto })
  //   }
  // }

  fetchData = (state, instance) => {

    // Whenever the table model changes, or the user sorts or changes pages, this method gets called and passed the current table model.
    // You can set the `loading` prop of the table to true to use the built-in one or show you're own loading bar if you want.
    this.setState({ loading: true });
    // Request the data however you want.  Here, we'll use our mocked service we created earlier
    requestData(state.pageSize, state.page, state.sorted, state.filtered, this.state.dto).then(
      res => {
        console.log(res.items);

        if (res.items) {
          // var pageSize = res.data.result.length > state.pageSize ? state.pageSize : res.data.result.length;
          // Now just get the rows of data to your React Table (and update anything else like total pages or loading)
          this.setState({
            data: res.items,
            // pages: res.totalPage,
            pages: res.meta.pagination.total_pages,
            totalRecords: res.meta.pagination.total_objects,
            loading: false,
            modal: false,
            searchModal: false,
            pageSize: state.pageSize,
          });
        }
      }
    ).catch((err) => {
      console.log(err);

      this.setState({ loading: false, searchModal: false });
    })
  }

  handleSweetAlertHide = () => {
    this.setState({ confirmDeleteShow: false });
  }

  handleError(error) {
    this.setState({ loading: false });
    // // toastr.warning('مشکلی پیش آمد', error);
  }

  // handleDelete = (e) => {
  //   let _model = this.state.data[e];

  //   this.setState({ confirmDeleteShow: true, model: _model });
  // }

  // handleConfirmDelete = () => {
  //   this.setState({ loading: true, confirmDeleteShow: false });
  //   // // console.log(this.state.model);
  //   productsService.delete(this.state.model.id)
  //     .then((res) => {
  //       toastr.success('حذف محصول ', 'محصول با موفقیت حذف شد');
  //       this.table.fireFetchData();
  //     })
  //     .catch((error) => {
  //       this.handleError(error)
  //     });
  // }

  _handleSearch = (e) => {
    let searcjQery = e.target.value.toLowerCase(),
      displayedItems = this.state.initialItems.filter((el) => {
        let searchValue = el.name.toLowerCase();
        return searchValue.indexOf(searcjQery) !== -1;
      })
    this.setState({
      data: displayedItems
    })
  }

  _handleCancelSearch = (e) => {
    document.getElementById("searchInput").value = '';
    this.setState({
      data: this.state.initialItems
    })
  }

  handleFilterChange = () => {

    const current = this.reactTable.getResolvedState()

    if (current) {
      const recordsCount = current.sortedData.length;
      this.setState({ recordsCount })
      // return false
      // console.log(recordsCount);
    }

  }

  handleModalCreate = () => {
    this.setState({ dialogTitle: 'ثبت محصول جدید', model: { name: '', image: '', brand: '', shop_id: '', count: '' } }, () => {
      this.setState({ modal: true });
    })
  }


  handleSearch = (values) => {
    // console.log(values);

    this.setState({ loading: true });

    var dto = {};
    if (values) {

      // // Object.keys(values).length > 0 &&
      //   Object.entries(values)
      //     .reduce((acc, [key, value]) => {
      //       dto[key] = value
      //     })
      this.setState({ dto: values }, () => {
        // console.log(dto);
        this.table.fireFetchData();
      })
    }

  }

  handleShowAll = () => {
    var dto = {};
    this.setState({ dto: dto }, () => {
      this.table.fireFetchData();
    })
  }

  handleModalEdit = (_model) => {
    console.log(_model);

    this.setState({ dialogTitle: 'ویرایش محصول [' + _model.name + ']', modal: true, model: _model });
  }

  handleModalQrCodes = (_model) => {
    console.log(_model);

    this.setState({ dialogTitle: 'مشاهده و چاپ کدهای محصول [' + _model.name + ']', qrCodeModal: true, model: _model });
  }

  handleSubmit = (values) => {
    this.setState({ loading: true });

    values["shop_id"] = this.props.id

console.log(JSON.stringify(values));

    if (values.id) {

      // productsService.edit(values.id, JSON.stringify(values))
      //   .then((res) => {

      //     toastr.success('ویرایش محصول', 'محصول "' + values.name + '" با موفقیت ویرایش شد');
      //     this.setState({ modal: false });
      //     this.table.fireFetchData();

      //   })
      //   .catch((error) => {
      //     this.handleError(error)
      //   });

    } else {

      shopsService.addProduct(values)
        .then((res) => {

          toastr.success('ثبت محصول جدید', 'محصول "' + res.data.name + '" با موفقیت ثبت شد');
          this.setState({ modal: false });
          this.table.fireFetchData();

        })
        .catch((error) => {
          this.handleError(error)
        });

    }
  }

  render() {
    const { loading, data, pages, pageSize, modal, searchModal, model, dialogTitle, dto } = this.state;
    // var recordsCount = 0;
    return (
      <div>
        <CSVLink
          data={this.state.dataToDownload}
          filename="data.csv"
          className="hidden"
          ref={(r) => this.csvLink = r}
          target="_blank" />

        <div className="row table-container" style={{ marginTop: 0 }}>
          <ReactTable
            manual
            data={data}
            pages={pages}
            loading={loading}
            onFetchData={this.fetchData}
            ref={(instance) => { this.table = instance; }}
            sorted={this.state.sorted}
            onSortedChange={(newSort, column) => {
              this.setState({ sorted: newSort });
            }}
            onPageSizeChange={pageSize => {
              // const { dispatch } = this.props;
              // dispatch( setPageSize(pageSize) )
              localStorage.setItem('pageSizeForProjects', pageSize);
              // this.setState({ page: 0 });
            }}
            page={this.state.page}
            onPageChange={page => this.setState({ page })}

            defaultPageSize={pageSize}
            previousText='صفحه قبل'
            nextText='صفحه بعد'
            loadingText='در حال بارگذاری ...'
            noDataText='نتیجه ای یافت نشد'
            pageText='صفحه'
            ofText='از'
            rowsText='تعداد نمایش'
            getTableProps={() => ({ style: { display: 'block' } })}
            getTrProps={(state, rowInfo, column, instance) => {
              const newSelected = Object.assign({}, this.state.selected);
              const selected = rowInfo ? newSelected[rowInfo.original.id] : false;
              return {
                style: {
                  backgroundColor: selected && "#5079F7",
                  color: selected && 'white',
                }
              }
            }}
            columns={[
              {
                Header: "ردیف",
                id: "row",
                sortable: false,
                maxWidth: 50,
                filterable: false,
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                className: "numberFa",
                Cell: (row) => {
                  return (row.pageSize * row.page) + (row.index + 1);
                }
              },
              {
                Header: 'عکس محصول',
                accessor: 'image',
                id: "image",
                width: 100,
                sortable: false,
                filterable: false,
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                Cell: row => (
                  <img style={{ height: 70, width: 80, display: 'inline' }} alt='' src={config.uploadsUrl + row.original.image} />
                ),
              },
              {
                Header: "نام محصول",
                accessor: "name",
                id: "name",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center', overflow: 'visible' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["name"], threshold: matchSorter.rankings.CONTAINS }),
                filterAll: true,
                Filter: ({ onChange }) =>
                  <Input placeholder="" allowClear onChange={event => onChange(event.target.value)} />
              },
              {
                Header: "برند",
                accessor: "brand",
                id: "brand",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center', overflow: 'visible' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["brand"], threshold: matchSorter.rankings.CONTAINS }),
                filterAll: true,
                Filter: ({ onChange }) =>
                  <Input placeholder="" allowClear onChange={event => onChange(event.target.value)} />
              },
              {
                Header: "تعداد بارکد",
                accessor: "item_count",
                id: "item_count",
                sortable: false,
                style: { display: 'flex', alignItems: 'center', overflow: 'visible', justifyContent: 'center' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["item_count"], threshold: matchSorter.rankings.CONTAINS }),
                filterAll: true,
                Filter: ({ filter, onChange }) =>
                  <Input placeholder="" allowClear onChange={event => onChange(event.target.value)} />,

                Cell: row => (
                  row.original.item_count
                )
              },
              {
                Header: "تاریخ ثبت",
                accessor: "created_at",
                id: "created_at",
                style: { display: 'flex', alignItems: 'center', overflow: 'visible', justifyContent: 'center' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["created_at"], threshold: matchSorter.rankings.CONTAINS }),
                filterAll: true,
                Filter: ({ filter, onChange }) =>
                  <Input placeholder="" allowClear onChange={event => onChange(event.target.value)} />,

                Cell: row => (
                  row.original.created_at
                )
              },
              {
                Header: "بارکدهای محصول",
                accessor: 'id',
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                sortable: false,
                width: 200,
                Filter: ({ filter, onChange }) =>
                  <Link to="/admin/products/new" className="btn btn-primary react-table-add-btn">
                    <span>
                      <FaPlus size="16px" />
                      <span>افزودن محصول جدید</span>
                    </span>
                  </Link>,
                Cell: row => (
                  <div>

                    <a onClick={() => this.handleModalQrCodes(row.original)} className="btn btn-sm btn-simple btn-info bmd-ripple bmd-flat-btn"><AiOutlineQrcode size="28px" /></a>

                    {/* <a onClick={() => this.handleModalEdit(row.original)} className="btn btn-sm btn-simple btn-success bmd-ripple bmd-flat-btn" rel="tooltip" data-original-title="ویرایش"><FiEdit size="24px" /></a>
                    <a onClick={() => this.handleDelete(row.index)} className="btn btn-sm btn-simple btn-danger bmd-ripple bmd-flat-btn delete-btn" data-original-title="حذف" rel="tooltip"><FiTrash2 size="24px" /></a> */}

                  </div>
                )
              }
            ]}
            defaultPageSize={10}
            className="-striped -highlight"
          >
            {(state, makeTable, instance) => {
              let recordsInfoText = "";

              const { filtered, pageRows, pageSize, sortedData, page, data } = state;

              if (sortedData && sortedData.length > 0) {
                let isFiltered = filtered.length > 0;

                let totalRecords = sortedData.length;

                let totalDataCount = data.length;

                let recordsCountFrom = page * pageSize + 1;

                let recordsCountTo = recordsCountFrom + pageRows.length - 1;

                if (isFiltered)
                  recordsInfoText = `${totalRecords} از ${totalDataCount} نتیجه فیلتر شده`;
                else
                  recordsInfoText = `تعداد کل: ${totalRecords}`;
              } else recordsInfoText = "نتیجه ای یافت نشد";

              return (
                <>
                  <div className="row">


                    <div className="col-lg-2 col-sm-12 flex-align-center flex-right">
                      <span className="records-info">{`تعداد کل: ${this.state.totalRecords}`}</span>
                    </div>
                    <div className="col-lg-2 col-sm-12">

                    </div>

                    <div className="col-lg-2 col-sm-12">

                    </div>


                    <div className="col-lg-6 col-sm-12 flex-align-center flex-left">
                      {/* <button onClick={(e) => this.download(e)} className="btn btn-sm btn-info pull-left export-btn" disabled={downloading}>
                          <span>
                            <span>Export to Excel &nbsp;</span>

                            {downloading ?
                              <AiOutlineLoading className="mdi-spin" size="20px" />
                              :
                              <FaFileExcel size="20px" style={{ verticalAlign: 'middle' }} />
                            }
                          </span>
                        </button> */}

                      <a onClick={() => this.handleModalCreate()} className="btn btn-primary pull-left btn-sm" disabled={loading} style={{ margin: 0, marginLeft: 5 }}>
                        <MdAdd size="20px" style={{ verticalAlign: 'middle' }} />&nbsp;ثبت محصول جدید</a>

                      {/* <a onClick={() => this.setState({ modalHeading: 'جستجو', searchModal: true })} className="btn btn-sm btn-primary pull-left" style={{ margin: 0, marginLeft: 5 }} disabled={loading}>
                            <GiMagnifyingGlass size="20px" style={{ verticalAlign: 'middle' }} />&nbsp;جستجو</a>

                          <a onClick={() => this.handleShowAll()} className="btn btn-primary pull-left btn-sm" disabled={loading} style={{ margin: 0, marginLeft: 5 }}>
                            <MdPlaylistAddCheck size="20px" style={{ verticalAlign: 'middle' }} />&nbsp;نمایش همه</a> */}

                    </div>


                  </div>

                  <div className="clearfix"></div>
                  <br />
                  {makeTable()}
                </>
              );
            }}
          </ReactTable>

        </div>

        <Dialog
          open={this.state.qrCodeModal}
          onClose={() => this.setState({ qrCodeModal: false })}
          TransitionComponent={Transition}
          scroll={'paper'}
          fullScreen={true}
          // maxWidth="lg"
          aria-labelledby="scroll-dialog-title"
          aria-describedby="scroll-dialog-description">
          <DialogTitle>
            <Fab color="secondary" aria-label="refresh" size="small" onClick={() => this.setState({ qrCodeModal: false })} className="modal-close-btn">
              <MdClose size="20px" />
            </Fab>
            <span className="dialogTitle">{dialogTitle}</span>
            <span style={{ marginRight: 10 }}></span>
          </DialogTitle>
          <DialogContent dividers={true}
            style={{ minHeight: 225 }}
          >
            <QrCodes onSubmit={() => this.handleModalClose()} data={this.state.model} onClose={() => this.handleModalClose()} isModal={true} />
          </DialogContent>
        </Dialog>

        <Dialog
          open={modal && !loading}
          TransitionComponent={Transition}
          keepMounted
          fullWidth={true}
          maxWidth="md"
          onClose={() => this.setState({ modal: false })}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"
        // style={{ overflow: 'visible' }}
        >

          <DialogTitle>
            <Fab color="secondary" aria-label="refresh" size="small" onClick={() => this.setState({ modal: false })} className="modal-close-btn">
              <MdClose size="20px" className={loading ? 'mdi-spin mdi-loading' : ''} />
            </Fab>
            <span className="dialogTitle">{dialogTitle}</span>
          </DialogTitle>
          <DialogContent dividers className="search-modal">

            {loading && <Loading />}

            <Formik
              enableReinitialize
              initialValues={model}
              onSubmit={(values, { setSubmitting }) => {
                // // console.log(values);
                this.handleSubmit(values)
              }}
              validationSchema={
                Yup.object().shape({
                  name: Yup.string().trim().required('نام محصول'),
                  count: Yup.string().trim().required('تعداد محصول'),
                })}
            >
              {props => {
                const {
                  values,
                  touched,
                  errors,
                  isSubmitting,
                  handleChange,
                  handleBlur,
                  setFieldValue,
                  handleSubmit,
                } = props;
                return (

                  <div className="row" style={{ position: "relative" }}>
                    <div className="col-md-12">
                      <form onSubmit={handleSubmit}>

                        <div className="row">
                          <div className="form-inline">

                            <div className="col-md-12">
                              <label className="pull-right">نام محصول</label>
                              <Field type="text"
                                autoFocus
                                name="name"
                                className={
                                  getIn(errors, "name") &&
                                    getIn(touched, "name")
                                    ? "text-input has-error rtl"
                                    : "text-input required rtl"
                                }
                              />
                              <ErrorMessage component={ErrorPopUp} name="name" />
                            </div>

                            <div className="clearfix form-row"></div>

                            <div className="col-md-6">
                              <label className="pull-right">برند</label>
                              <Field type="text"
                                name="brand"
                                className={
                                  getIn(errors, "brand") &&
                                    getIn(touched, "brand")
                                    ? "text-input has-error rtl"
                                    : "text-input required rtl"
                                }
                              />
                              <ErrorMessage component={ErrorPopUp} name="brand" />
                            </div>

                            <div className="col-md-6">
                              <label className="pull-right">تعداد محصول</label>
                              <DecimalInput type='tel'
                                value={values.count}
                                defaultValue={values.count}
                                onChange={(e) => {
                                  setFieldValue('count', e.target.value);
                                }}
                                className={
                                  getIn(errors, "count") &&
                                    getIn(touched, "count")
                                    ? "text-input has-error rtl"
                                    : "text-input required rtl"
                                }
                              />
                              <ErrorMessage component={ErrorPopUp} name="count" />
                            </div>

                            <div className="clearfix form-row"></div>

                            <div className="col-md-12">
                              <label className="pull-right">تصویر</label>
                              <PicUploader label="تصویر"
                                value={values.image}
                                defaultValue={values.image}
                                onChange={(val) => {
                                  setFieldValue('image', val);
                                }}
                              />
                              <ErrorMessage component={ErrorPopUp} name="image" />
                            </div>

                            <div className="col-md-12">
                              <br />
                              <input type="hidden" name="id" value={values.id} />
                              <button type="submit" className={"icon button submit-btn " + (isSubmitting ? "isSubmitting" : "")} disabled={isSubmitting}>
                                {isSubmitting ?
                                  <AiOutlineLoading className="mdi-spin" size="22px" />
                                  :
                                  <AiOutlineSave size="22px" />
                                }ذخیره</button>
                            </div>

                          </div>
                        </div>


                      </form>
                    </div>
                  </div>

                );
              }}
            </Formik>

          </DialogContent>
        </Dialog>



        <Dialog
          open={searchModal && !loading}
          TransitionComponent={Transition}
          keepMounted
          fullWidth={true}
          maxWidth="md"
          onClose={() => this.setState({ dialogTitle: 'جستجو', searchModal: false })}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"
        // style={{ overflow: 'visible' }}
        >

          <DialogTitle>
            <Fab color="secondary" aria-label="refresh" size="small" onClick={() => this.setState({ searchModal: false })} className="modal-close-btn">
              <MdClose size="20px" className={loading ? 'mdi-spin mdi-loading' : ''} />
            </Fab>
          </DialogTitle>
          <DialogContent dividers className="search-modal">

            {loading && <Loading />}

            <Formik
              enableReinitialize
              initialValues={this.state.dto}
              onSubmit={(values, { setSubmitting }) => {
                // // console.log(values);
                this.handleSearch(values);
                setSubmitting(false);
              }}
            // validationSchema={ValidationSchema}
            >
              {props => {
                const {
                  values,
                  touched,
                  errors,
                  isSubmitting,
                  handleChange,
                  handleBlur,
                  setFieldValue,
                  handleSubmit,
                  resetForm,
                } = props;
                return (


                  <div className="row" style={{ position: "relative" }}>
                    <div className="col-md-12">
                      <form onSubmit={handleSubmit}>
                        {/* {JSON.stringify(values)} */}

                        <div className="row">
                          <div className="form-inline search-container">

                            <div className="col-lg-4 col-sm-12">
                              <label>استان</label>
                              <ProvinceSelect
                                isClearable
                                defaultValue={values && values.shop_id ? values.shop_id : undefined}
                                onChange={(val) => {
                                  setFieldValue('shop_id', val ? val.value : undefined);
                                  !val && setFieldValue('city_id', undefined);
                                }}
                              />
                            </div>

                            <div className="col-lg-4 col-sm-12">
                              <label>شهرستان</label>
                              <CitySelect
                                isClearable
                                shop_id={values.shop_id}
                                defaultValue={values && values.shop_id && values.city_id ? values.city_id : undefined}
                                onChange={(val) => {
                                  setFieldValue('city_id', val ? val.value : undefined);
                                }}
                              />
                            </div>

                            <div className="col-lg-4 col-sm-12">
                              <label>نام محصول</label>

                              <input type="text"
                                onChange={e => {
                                  setFieldValue("name", e.target.value)
                                }}
                                value={values && values.name ? values.name : ''}
                                name="name"
                                className="text-input"
                              />

                            </div>

                            <div className="clearfix form-row"></div>

                            <div className="col-md-12">
                              <button type="submit" className="btn btn-primary " disabled={isSubmitting} >
                                {loading ?
                                  <i className="mdi mdi-spin mdi-loading"></i>
                                  :
                                  <i className="mdi mdi-magnify"></i>
                                }
                                &nbsp;
                                جستجو
                                &nbsp;
                          </button>

                            </div>
                          </div>
                        </div>
                      </form>
                    </div>
                  </div>
                );
              }}
            </Formik>

          </DialogContent>
        </Dialog>



        <MySweetAlert
          show={this.state.confirmDeleteShow}
          onCancel={this.handleSweetAlertHide}
          onConfirm={this.handleConfirmDelete}
          title={"آیا از حذف این محصول مطمئن هستید؟"}
        >
        </MySweetAlert>

        {loading ? <Loading /> : ''}
      </div>
    );
  }
}

export default Products;
