import React, { useState, useEffect } from 'react'

import { toastr } from 'react-redux-toastr'
import { Field, FastField, ErrorMessage, getIn, Formik } from "formik"
import * as Yup from 'yup'
import ErrorPopUp from '../Shared/ErrorPopUp'
import { AiOutlineLoading, AiOutlineSave } from "react-icons/ai";

import GiftSelect from '../Shared/GiftSelect'
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import { shopsService } from '../../_services/shops.service'

import Fab from '@material-ui/core/Fab'
import { MdClose } from 'react-icons/md'

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const AssignGift = props => {

  const [loading, setLoading] = useState(false)
  const [data, setData] = useState({ uuid: '', gift_id: '' })
  const [open, setOpen] = useState(false)
  const [index, setIndex] = useState(props.index)

  useEffect(() => {

    console.log(props.data);

    if (props.data != undefined) {

      let d = props.data;
      d["gift_id"] = '';

      setData(d)

      setOpen(props.open)

    }

  }, [props.open])

  const handleAssignGift = (values) => {

    console.log(JSON.stringify(values));


    shopsService.assignGift(JSON.stringify(values))
      .then((res) => {

        console.log(res.data.item_code);

        toastr.success('ثبت جایزه برای محصول انجام شد');

        let tmp = res.data.item_code;
        tmp["index"] = data.index

        console.log(tmp);
        

        props.onCloseAndReload(tmp)

      })
      .catch((error) => {
        console.log(error)
      });

  }


  return (
    <div>
      <Dialog
        open={open && !loading}
        TransitionComponent={Transition}
        keepMounted
        fullWidth={true}
        maxWidth="xs"
        onClose={() => props.onClose()}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      // style={{ overflow: 'visible' }}
      >

        <DialogTitle>
          <Fab color="secondary" aria-label="refresh" size="small" onClick={() => props.onClose()} className="modal-close-btn">
            <MdClose size="20px" className={loading ? 'mdi-spin mdi-loading' : ''} />
          </Fab>
          <span className="dialogTitle">{'انتخاب جایزه'}</span>
        </DialogTitle>
        <DialogContent dividers className="search-modal">

          <Formik
            enableReinitialize
            initialValues={data}
            onSubmit={(values, { setSubmitting }) => {
              // // console.log(values);
              handleAssignGift(values)
            }}
            validationSchema={
              Yup.object().shape({
                uuid: Yup.string().trim().required('کد محصول'),
                gift_id: Yup.string().trim().required('جایزه'),
              })}
          >
            {props => {
              const {
                values,
                touched,
                errors,
                isSubmitting,
                handleChange,
                handleBlur,
                setFieldValue,
                handleSubmit,
              } = props;
              return (

                <div className="row" style={{ position: "relative" }}>
                  <div className="col-md-12">
                    <form onSubmit={handleSubmit}>

                      <div className="row">
                        <div className="form-inline">

                          <div className="col-md-12">
                            <label className="rtl">انتخاب جایزه</label>
                            <GiftSelect
                              required
                              defaultValue={values.gift_id}
                              onChange={(val) => {
                                setFieldValue('gift_id', val);
                              }}
                            />
                            <ErrorMessage component={ErrorPopUp} name="gift_id" />
                          </div>

                          <div className="col-md-12">
                            <br />
                            <input type="hidden" name="id" value={values.id} />
                            <button type="submit" className={"icon button submit-btn " + (isSubmitting ? "isSubmitting" : "")} disabled={isSubmitting}>
                              {isSubmitting ?
                                <AiOutlineLoading className="mdi-spin" size="22px" />
                                :
                                <AiOutlineSave size="22px" />
                              }ذخیره</button>
                          </div>

                        </div>
                      </div>


                    </form>
                  </div>
                </div>

              );
            }}
          </Formik>

        </DialogContent>
      </Dialog>
    </div>
  )
}

export default AssignGift;